@use '../util' as *;

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, min-content);
  align-items: center;
  justify-items: start;
  gap: rem(23);

  img,
  .card__change {
    justify-self: end;
  }

  .card__count {
    margin-bottom: 0;
  }

  .card__change,
  .card__count {
    align-self: flex-end;
  }
}
